<template>
  <div class="g-classifyDetails scroll-list-wrap">
    <cube-scroll
            ref="scroll"
            :data="list"
            :options="options"
            @pulling-down="onPullingDown"
            @pulling-up="onPullingUp">
    <div class="bit">
      <router-link v-for="(v,i) in list" :key="i" :to="{name:'detail',query:{id:v.id}}" class="m-box" tag="div">
        <div class="s-img">
          <img v-lazy="'https://fx18.oss-cn-hangzhou.aliyuncs.com/'+v.carousels[0]" alt="">
        </div>
        <div class="s-msg beyond">{{v.proName}}</div>
        <div class="s-ps">
          <span>积分：{{v.price}}</span>
          <span>PV:{{v.integral}}</span>
        </div>
      </router-link>
    </div>
    </cube-scroll>
    <nocontent v-show="list.length==0"/>
  </div>
</template>

<script>
  import nocontent from "@/components/nocontent"
  export default {
    name: "classifyDetails",
    data(){
      return{
        bit:{
          sortId:1,
          categoryId:this.$route.query.categoryId||'',
          shopId:this.$route.query.shopId||'',
          keyword:this.$route.query.keyword||'',
          page:1
        },
        list: [],/*商品数据*/
        options: {
          /*滚动配置*/
          scrollbar: {fade: true},
          pullUpLoad: {txt: {noMore: '没有更多数据了'}},
          pullDownRefresh: {stopTime: 0, txt: ' '}
        }
      }
    },
    created() {
      this.class();//商品详情
    },
    methods:{
      class(){
        this.$post(this.$bee+'/api/product/listProduct',this.bit).then((data)=>{
          if(data.code==200&&data.data.pros.length!=0){
            data.data.pros.forEach((i) => {
              this.list.push(i)
            })
            this.bit.page++;
          }else{
            this.$refs.scroll.forceUpdate()
          }
        })
      },
      onPullingDown() {/*下拉刷新*/
        this.bit.page=1;
        this.list=[];
        this.class();
      },
      onPullingUp() {/*上拉加载*/
        this.class();
      }
    },
    beforeRouteEnter(to,from,next){
      if(to.query.titlename){
        to.meta.title=to.query.titlename
      }else{
        to.meta.title='分类详情'
      }
      next();
    },
    components: {
      nocontent
    }
  }
</script>

<style scoped lang="stylus">
  .bit
    display flex
    flex-wrap wrap
    padding 7.5px 5px 0
    .m-box
      box-sizing border-box
      width 172.5px
      overflow hidden
      margin 0 5px
      padding-bottom 12.5px
      box-shadow: 0px 5px 13.5px 1.5px rgba(84, 84, 85, 0.3)
      margin-bottom 10px
      .s-img
        width 100%
        img
          width 100%
          height 165px
      .s-msg
        font-size 13.5px
        padding 5px 5px
        margin-top 7.5px
        text-align left
        color #5c5b5b
      .s-ps
        text-align left
        padding 0 5px
        span
          font-size: 14.5px
          color #c38b30
          &:nth-child(2)
            margin-left 10px
</style>
